@use "sass:math";
@import "../../styles/querys";
@import "../../styles/null";
@import "../../styles/variables";

.fifthPage {
	position: relative;
	min-height: 100vh;
	&__video {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	&__infoCnt {
		position: relative;
		z-index: 2;
		display: flex;
		justify-content: space-between;
		align-items: center;
		min-height: 100vh;
		padding: 0 10rem;
		color: #fff;
		@media (max-width: 1200px) {
			padding: 0 5rem;
		}
		@media (max-width: $mobile) {
			padding: 0 rem(10);
		}
	}

	&__leftInfo,
	&__rightInfo {
		width: 20%;
		line-height: 1.3;
		font-weight: 700;
		@media (max-width: $mobileSmall) {
			width: 30%;
		}
		h3 {
			padding: 3rem 0;
			color: rgb(210, 210, 210);
		}
		span {
			@include adaptiveValue("font-size", 24, 10);
		}
	}

	&__leftInfo {
	}

	&__rightInfo {
	}
}
