@use "sass:math";
@import "../../styles/querys";
@import "../../styles/null";
@import "../../styles/variables";

.secondPage {
	min-height: 125vh;
	color: rgba(255, 255, 255, 0.6);
	display: flex;
	flex-direction: column;
	justify-content: center;
	position: relative;
	&__video {
		position: absolute;
		height: 100%;
		width: 100vw;
		object-fit: cover;
		z-index: 1;
	}
	&__textBox {
		padding: 5% 30%;
		position: relative;
		@include adaptiveValue("font-size", 56, 30);
		font-weight: 700;
		z-index: 2;
		@media (max-width: $tablet) {
			padding: 5% 20%;
		}
		@media (max-width: $mobile) {
			padding: 5% 10%;
		}
	}

	&__textCnt {
		line-height: 1.3;
		text-align: center;
	}

	&__textHighlight {
	}
}
