@use "sass:math";
@import "../../styles/querys";
@import "../../styles/null";
@import "../../styles/variables";

.forthPage {
	position: relative;
	display: flex;
	min-height: 100vh;
	background-color: #fff;
	overflow: hidden;
	&__purchaseLeft {
		display: flex;
		flex-direction: column;
		flex: 1;
		overflow: hidden;
		align-items: center;
		justify-content: center;
	}

	&__phoneGallery {
		display: flex;
		align-self: flex-start;
	}

	&__galleryCnt {
		min-width: 50vw;
		display: flex;
		justify-content: center;
		align-items: center;
		img {
			transform: scale(0.8);
			@media (max-width: $mobile) {
				transform: scale(0.5);
				max-width: 50vw;
				min-width: auto;
			}
			@media (max-width: $mobileSmall) {
				transform: scale(0.4);
			}
		}
	}

	&__imgBlueBack {
	}

	&__imgSilverBack {
	}

	&__imgGoldBack {
	}

	&__imgGraphiteBack {
	}

	&__swatchCnt {
		text-align: center;
		span {
			font-size: rem(8);
		}
		@media (max-width: $mobileSmall) {
			position: relative;
			top: -10%;
		}
	}

	&__swatches {
		img {
			padding: 0.5rem 0.3rem;
			cursor: pointer;
		}
	}

	&__purchaseRight {
		flex: 1;
		position: relative;
		background: black;
		img {
			position: absolute;
			bottom: 0;
			left: 50%;
			transform: translate(-50%, 0) scale(0.8);
			transform-origin: bottom;
			@media (max-width: $tablet) {
				transform: translate(-50%, 0) scale(0.6);
			}
			@media (max-width: $mobile) {
				transform: translate(-40%, 0) scale(0.6);
			}
			@media (max-width: $mobileSmall) {
				transform: translate(-35%, 0) scale(0.6);
			}
		}
	}

	&__imgBlue {
		z-index: 1;
	}

	&__imgSilver {
	}

	&__imgGold {
	}

	&__imgGraphite {
	}
}
