@use "sass:math";
@import "../../styles/querys";
@import "../../styles/null";
@import "../../styles/variables";

.firstPage {
	min-height: 100vh;
	padding: 0 20%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	@media (max-width: $tablet) {
		padding: 0 10%;
	}
	&__text {
		position: relative;
		z-index: 2;
		text-align: center;
		padding-bottom: 160px;
	}

	&__title {
		@include adaptiveValue("font-size", 24, 18);
		font-weight: 700;
	}

	&__subtitle {
		font-weight: 700;
		@include adaptiveValue("font-size", 50, 36);
	}

	&__video {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		@media (max-width: $tablet) {
			top: 15%;
		}
		@media (max-width: $mobile) {
			top: 50%;
		}
	}
}
