@use "sass:math";
@import "../../styles/querys";
@import "../../styles/null";
@import "../../styles/variables";

.header {
	&__menu {
		position: fixed;
		width: 100vw;
		z-index: 10;
		display: flex;
		align-items: center;
		justify-content: space-between;
		gap: rem(10);
		min-height: 8vh;
		margin-block: auto;
		padding: 0 20%;
		background-color: rgba(255, 255, 255, 0.6);
		backdrop-filter: blur(10px);
		@media (max-width: $tablet) {
			padding: 0 10%;
		}

		@media (max-width: $mobile) {
			padding: 0 5%;
		}
	}

	&__logo {
		color: #000;
		@include adaptiveValue("font-size", 16, 10);
		font-weight: 700;
		white-space: nowrap;
	}

	&__navLinks {
		display: flex;
		align-items: center;
		@include adaptiveValue("gap", 25, 10);
		@include adaptiveValue("font-size", 12, 8);
	}

	&__btnBuy {
		@include adaptiveValue("padding-top", 5, 2);
		@include adaptiveValue("padding-bottom", 5, 2);
		@include adaptiveValue("padding-left", 30, 10);
		@include adaptiveValue("padding-right", 30, 10);
		@include adaptiveValue("font-size", 10, 8);
		color: #fff;
		background-color: #3056f5;
		border-radius: rem(16);
		cursor: pointer;
	}
}
