@use "sass:math";
@import "../../styles/querys";
@import "../../styles/null";
@import "../../styles/variables";

.sixthPage {
	position: relative;
	display: flex;
	justify-content: space-between;
	column-gap: 10px;
	min-height: 100vh;
	padding: 10% 20%;
	@media (max-width: 1100px) {
		padding: 10% 15%;
	}
	@media (max-width: $tablet) {
		padding: 5% 10%;
	}

	&__photoDescr {
	}

	&__photoTitle {
		@include adaptiveValue("font-size", 32, 20);
	}

	&__pthotoSubtitle {
		@include adaptiveValue("font-size", 64, 32);
	}

	&__portraitCnt {
	}

	&__portrait {
		@include adaptiveValue("max-width", 450, 200);
	}

	&__phoneVideo {
		position: absolute;
		z-index: 2;
		top: 35%;
		left: 20%;
		transform: scale(0.8);
		@media (max-width: $tablet) {
			top: 20%;
			left: 15%;
		}
		@media (max-width: $mobile) {
			left: 5%;
		}
		img,
		video {
			position: absolute;
			top: 0;
			left: 0;
		}
		img {
			z-index: 2;
			bottom: 0;
			left: 0;
			transform: scale(0.8);
		}
		video {
			left: 1rem;
			top: 1rem;
			transform: scale(0.82);
			border-radius: 2.5rem;
		}
	}
}
