@use "sass:math";
@import "../../styles/querys";
@import "../../styles/null";
@import "../../styles/variables";

.thirdPage {
	position: relative;
	min-height: 100vh;
	padding: 0 20%;
	@media (max-width: 1300px) {
		padding: 0 10%;
	}
	@media (max-width: $tablet) {
		padding: 0 10%;
	}
	@media (max-width: $mobile) {
		padding: 0 5%;
	}
	&__textCnt {
		display: flex;
		justify-content: space-around;
		align-items: center;
		min-height: 100%;
		background-color: #fff;
		@media (max-width: $mobile) {
			align-items: flex-end;
			padding-bottom: rem(160);
		}
	}

	&__textLeft {
	}

	&__phoneTitle {
	}

	&__phoneSize {
		font-weight: 700;
		@include adaptiveValue("font-size", 36, 20);
		&_pro {
			text-align: right;
		}

		&_max {
			text-align-last: left;
		}
	}

	&__textRight {
	}

	&__productImages {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
	}

	&__imgLarge {
		position: relative;
		left: 0;
		top: 0;
		transform: scale(0.8);
		z-index: 1;
		@media (max-width: $mobile) {
			transform: scale(0.5);
		}
		@media (max-width: $mobileSmall) {
			transform: scale(0.4);
		}
	}

	&__imgSmall {
		position: absolute;
		top: rem(60);
		left: rem(60);
		transform: scale(0.8);
		z-index: 2;
		@media (max-width: $mobile) {
			top: rem(50);
			transform: scale(0.5);
		}
		@media (max-width: $mobileSmall) {
			transform: scale(0.4);
		}
	}

	&__retinaLine {
		position: absolute;
		bottom: 15%;
		left: 50%;
		transform: translate(-50%, -15%);
		font-weight: 700;
		@include adaptiveValue("font-size", 16, 10);
	}
}
